<script>
import { useAccountTypeStore } from '@/stores/accountTypeStore.js';
import BaseLayout from '@/layouts/BaseLayout.vue';
import DownloadCard from '@/components/DownloadCard.vue'
import QuickstartIndividual from '@/docs/quickstart/QuickstartIndividual.vue'
import QuickstartInstitutional from '@/docs/quickstart/QuickstartInstitutional.vue'
import AccountToggle from '@/components/AccountToggle.vue'
export default {
  components: {
    BaseLayout,
    DownloadCard,
    QuickstartIndividual,
    QuickstartInstitutional,
    AccountToggle
  },
  computed: {
    activeTab() {
      const store = useAccountTypeStore()
      return (store.accountType == 'individual') ? 'QuickstartIndividual' : 'QuickstartInstitutional';
    }
  },
  data() {
    return {
      downloads: [
        {
          title: 'API Gateway',
          content: 'Download the latest version of the Client Portal API Gateway',
          url: 'https://download2.interactivebrokers.com/portal/clientportal.gw.zip'
        },
        {
          title: 'Java',
          content: 'Download the latest version of the Java Runtime Environment',
          url: 'https://www.java.com/en/download/'
        }
      ]
    }
  },
}
</script>

<template>
  <base-layout>
    <template #content>
      <div class="content-header">
        <h2>Quickstart Guide</h2>
        <account-toggle id="active"/>
      </div>
      <component :is="activeTab" />
    </template>
    <template #aside>
      <div class="downloads">
        <h2>Downloads</h2>
        <download-card v-for="download in downloads" v-bind="download" />
      </div>
    </template>
  </base-layout>
</template>

<style scoped>
.content-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.content-header:last-child {
  display: none;
}

.downloads {
  display: flex;
  flex-direction: column;
  margin-inline: 1.5rem;
}

#active {
  display: none;
}

@media only screen and (min-width: 700px) {
  #active {
    display: block;
  }

  .downloads {
    margin-bottom: 3rem;
  }
}

@media only screen and (min-width: 1200px) {
  .downloads {
    margin-inline: 0;
    margin-right: 1.5rem;
    margin-block: 3rem;
  }
}
</style>